.logo {
  $properties-for-transition: opacity;

  @include transition-settings($properties-for-transition);


  // --- Интерактивные стили ---
  @include non-touch { 
    &:hover {
      opacity: 0.7;
    }
  }

  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}
